import React from "react";
import {
    Navbar,
    NavbarBrand,
    Collapse,
    NavbarToggler,
    Nav,
    NavItem,
    NavLink,
    UncontrolledDropdown,
    DropdownToggle,
    DropdownItem,
    DropdownMenu,
} from "reactstrap";
import {Link, useLocation} from 'react-router-dom';

function NavigationApp() {
    const docs = 'https://help.omegatheme.com/en/category/facebook-events-by-omega-yqc7xu/'
    const [isOpen, setIsOpen] = React.useState(false);
    const pathname = useLocation().pathname;
    const list_pathName = [
        '/general-settings',
        '/label-custom',
        '/calender-snippet',
        '/advanced-settings',
    ]
    return(
        <div>
            <Navbar color="light" expand="md" light>
                <NavbarBrand href="/">
                    Facebook Events
                </NavbarBrand>
                <NavbarToggler onClick={() => { setIsOpen(!isOpen) }} />
                <Collapse navbar isOpen={isOpen}>
                    <Nav className="me-auto" navbar>
                        <NavItem>
                            <NavLink className={`${pathname === '/' ? 'activeLink' : 'inActiveLink'}`} tag={Link} to="/facebook-events/build/">
                                Connect-Facebook
                            </NavLink>
                        </NavItem>
                        <UncontrolledDropdown inNavbar nav>
                            <DropdownToggle className={`${ list_pathName.includes(pathname) ? 'activeLink' : 'inActiveLink' }`} caret nav>
                                Settings
                            </DropdownToggle>
                            <DropdownMenu end>
                                <DropdownItem>
                                    <NavLink className={`${pathname === '/general-settings' ? 'activeLink' : 'inActiveLink'}`} tag={Link} to="/facebook-events/build/general-settings">
                                        General settings
                                    </NavLink>
                                </DropdownItem>
                                <DropdownItem>
                                    <NavLink className={`${pathname === '/label-custom' ? 'activeLink' : 'inActiveLink'}`} tag={Link} to="/facebook-events/build/label-custom">
                                        Label custom
                                    </NavLink>
                                </DropdownItem>
                                <DropdownItem>
                                    <NavLink className={`${pathname === '/calender-snippet' ? 'activeLink' : 'inActiveLink'}`} tag={Link} to="/facebook-events/build/calender-snippet">
                                        Calender snippet
                                    </NavLink>
                                </DropdownItem>
                                <DropdownItem>
                                    <NavLink className={`${pathname === '/advanced-settings' ? 'activeLink' : 'inActiveLink'}`} tag={Link} to="/facebook-events/build/advanced-settings">
                                        Advanced settings
                                    </NavLink>
                                </DropdownItem>
                                <DropdownItem>
                                    <NavLink tag={Link} to="/facebook-events/build/preview">
                                        Preview
                                    </NavLink>
                                </DropdownItem>
                            </DropdownMenu>
                        </UncontrolledDropdown>
                        <NavItem>
                            <NavLink className={`${pathname === '/events' ? 'activeLink' : 'inActiveLink'}`} tag={Link} to="/facebook-events/build/events">
                                Events
                            </NavLink>
                        </NavItem>
                        <NavItem>
                            <NavLink className="link_bank_docs">
                                <a target="__blank" href={docs}>Our Document</a>
                            </NavLink>
                            
                        </NavItem>
                    </Nav>
                </Collapse>
            </Navbar>
        </div>
    );
}

export default NavigationApp;
