import React, { useState } from 'react'
import {Page, Layout, Card} from "@shopify/polaris";
import { useDispatch } from 'react-redux';
import { openNavbarSaveChange } from '../../redux/Action';

function GeneralSettings() {
    // Create initial state
    const [valueColorText, setValueColorText] = useState('#fdffff');
    const [valueColorBackground, setValueColorBackground] = useState('#fdffff');
    const dispatch = useDispatch();

    // Func handle
    const handleChangeColorText = (e) => {
      setValueColorText(e.target.value);
    }

    const handleChangeColorBackground = (e) => {
      setValueColorBackground(e.target.value)
    }

    const handleChangeOpenSave = (evt) => {
      const value = evt.target.value;
      if (value) 
        dispatch(openNavbarSaveChange(true))
    }

    return (
        <Page fullWidth>
            <Layout>
                <Layout.Section>
                    <Card title="General Settings" sectioned>
                    <div className="form-group">
                      <label className="my-1 mr-2" >View Mode <span className='important_input'>*</span></label>
                      <select onChange={handleChangeOpenSave} className="custom-select my-1 mr-sm-2" >
                        <option selected>List View</option>
                        <option value="1">Grid View</option>
                      </select>
                    </div>
                    <div className="form-group">
                      <label for="pageName">Page Name</label>
                      <input onChange={handleChangeOpenSave} type="text" className="form-control" id="pageName" placeholder="Facebook Events 1" />
                    </div>
                    <div className="form-group">
                      <div className="form-check">
                        <input onChange={handleChangeOpenSave} className="form-check-input" type="checkbox" value="" id="invalidCheck1" required />
                        <label className="form-check-label" for="invalidCheck1">
                          Show Filter button
                        </label>
                      </div>
                      <div className="form-check">
                        <input onChange={handleChangeOpenSave} className="form-check-input" type="checkbox" value="" id="invalidCheck2" required />
                        <label className="form-check-label" for="invalidCheck2">
                        Show Search Bar
                        </label>
                      </div>
                    </div>
                    <div className="form-group">
                      <label for="event-limit">Event limit per page</label>
                      <input onChange={handleChangeOpenSave} type="number" className="form-control" id="event-limit" />
                    </div>
                    <div className="form-group">
                      <label className="my-1 mr-2" for="inlineFormCustomSelectPref">Ordering Events <span className='important_input'>*</span></label>
                      <select onChange={handleChangeOpenSave} className="custom-select my-1 mr-sm-2" id="inlineFormCustomSelectPref">
                        <option selected>Show from older to newer</option>
                        <option value="1">Show from older to older</option>
                      </select>
                    </div>
                    <div className="form-group">
                      <div className="form-check">
                        <input onChange={handleChangeOpenSave} className="form-check-input" type="checkbox" value="" id="invalidCheck3" required />
                        <label className="form-check-label" for="invalidCheck3">
                        Show Old Events
                        </label>
                      </div>
                      <div className="form-check">
                        <input onChange={handleChangeOpenSave} className="form-check-input" type="checkbox" value="" id="invalidCheck4" required />
                        <label className="form-check-label" for="invalidCheck4">
                        Show Event place
                        </label>
                      </div>
                      <div className="form-check">
                        <input onChange={handleChangeOpenSave} className="form-check-input" type="checkbox" value="" id="invalidCheck5" required />
                        <label className="form-check-label" for="invalidCheck5">
                        Show Event description
                        </label>
                      </div>
                      <div className="form-check">
                        <input onChange={handleChangeOpenSave} className="form-check-input" type="checkbox" value="" id="invalidCheck6" required />
                        <label className="form-check-label" for="invalidCheck6">
                        Show "Buy ticket" button
                        </label>
                      </div>
                    </div>
                    <div className="form-group">
                      <label className="my-1 mr-2" for="inlineFormCustomSelectPref">Show Event date</label>
                      <select onChange={handleChangeOpenSave} className="custom-select my-1 mr-sm-2" id="inlineFormCustomSelectPref">
                        <option selected>Both start and end date</option>
                        <option value="1">Only start date</option>
                        <option value="1">Hide event date</option>
                      </select>
                    </div>
                    <div className="form-group">
                      <label for="seeAllEvents">See All Events</label>
                      <input onChange={handleChangeOpenSave} type="text" className="form-control" id="seeAllEvents" />
                    </div>
                    <div className="form-group">
                      <label for="event-limit">Cover image height (px)</label>
                      <input onChange={handleChangeOpenSave} type="number" className="form-control" id="event-limit" />
                    </div>
                    <div className="form-group">
                      <label for="seeAllEvents">Readmore text custom</label>
                      <input onChange={handleChangeOpenSave} type="text" className="form-control" id="seeAllEvents" placeholder="readmore" />
                    </div>
                    <div className="form-group">
                      <div className="colorpicker">
                        <label className='mr-3' for="section1backgroundColor">Readmore background color :</label>
                        <input onChange={handleChangeOpenSave} type="color" id="section1backgroundColor" onInput={(e) => handleChangeColorBackground(e)} name="head" value={valueColorBackground} />
                      </div>
                      <div className="colorpicker">
                        <label className='mr-3' for="section1ParagraphColor">Readmore text color :</label>
                        <input onChange={handleChangeOpenSave} type="color" id="section1ParagraphColor" onInput={(e) => handleChangeColorText(e)} name="head" value={valueColorText}  />
                      </div>
                    </div>
                    <button type="button" className="btn btn-primary">Save Setting</button>
                    </Card>
                </Layout.Section>
            </Layout>
        </Page>
    )
}

export default GeneralSettings
